//  https://www.smashingmagazine.com/2015/11/using-system-ui-fonts-practical-guide/
/**
 * Safari for OS X and iOS (San Francisco)
  -apple-system,
  Chrome for OS X (San Francisco)
  BlinkMacSystemFont,
  Windows
  "Segoe UI",
  Android
  "Roboto",
  Linux
  "Oxygen", // KDE
  "Ubuntu",
  "Cantarell", // GNOME
  Firefox OS [R.I.P.]
  "Fira Sans",
  Older Android
  "Droid Sans",
  Basic web fallback
  "Helvetica Neue", Arial, sans-serif !default;
**/
$font-family-sans-serif: -apple-system, system-ui, BlinkMacSystemFont, "Segoe UI", Roboto, "Helvetica Neue", Arial, sans-serif !default;
$font-family-serif: Georgia, "Times New Roman", Times, serif !default;
$font-family-monospace: Menlo, Monaco, Consolas, "Liberation Mono", "Courier New", monospace !default;
$font-family-base: $font-family-sans-serif !default;

//
$font-family-cn: "PingFang SC", "Hiragino Sans GB", "Microsoft YaHei" !default;

// rem  16px
$font-size-root: 14px !default;

$font-size-base: 1rem !default;
$font-size-lg: 1.25rem !default;
$font-size-sm: .875rem !default;
$font-size-xs: .75rem !default;

$line-height-base: 1.5 !default;

// font weight
/**
*  font weight 
* 1.  Safari  IOS 
    100-300   400-500  600-700  800-900  
    lighter ()
    normal 400
    bold  700
    bolder  ()
  -apple-system  "PingFang SC"
  100 200 300 400 500 600-900 
   lighter  100    normal  400    bold  700    bolder  700

*  2  Chrome 
   100-400 500-900  normal  bold lighter normal  bolder  bold
    "PingFang SC" 
*  font-weight 
  https://developer.mozilla.org/zh-CN/docs/Web/CSS/font-weight
**/

$font-weight-lighter: lighter !default;
$font-weight-2: 200 !default;
$font-weight-3: 300 !default;
$font-weight-normal: normal !default;
$font-weight-5: 500 !default;
$font-weight-bold: bold !default;
$font-weight-bolder: bolder !default;

$font-weight-base: $font-weight-normal !default;

$dt-font-weight: $font-weight-bold !default;
